import { FirebaseAPI } from "@/firebaseAPI/firebaseAPI";
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

export class FirebaseStorage extends FirebaseAPI {
  constructor(dbName) {
    super(dbName);
    this._storage = getStorage(this._firebase);
  }

  async addImageToStorage(image, folder = "") {
    const pathToImage = folder ? `/${folder}/${image.name}` : image.name;
    const storageRef = ref(this._storage, pathToImage);
    const snapshot = await uploadBytes(storageRef, image);
    return snapshot.ref.fullPath;
  }

  async getImageFromStorage(path) {
    const pathReference = ref(this._storage, path);
    const url = await getDownloadURL(pathReference);
    return url;
  }
}
