<template>
  <loader-spinner v-if="isLoad" />
  <work-preview v-for="work in works" :key="work.slug" :work-params="work" />
</template>
<script>
import LoaderSpinner from "./LoaderSpinner.vue";
import WorkPreview from "./WorkPreview.vue";
import { FirebaseAPI } from "@/firebaseAPI/firebaseAPI";
import { FirebaseStorage as WorksStorage } from "@/firebaseAPI/firebaseStorage";
import { stringToCapitilize } from "@/utils/utils";

export default {
  mounted() {
    this.getWorks();
  },
  data() {
    return {
      dbWork: new FirebaseAPI("works"),
      dbTech: new FirebaseAPI("techList"),
      storeWork: new WorksStorage("works"),
      works: [],
      isLoad: true,
    };
  },
  components: {
    WorkPreview,
    LoaderSpinner,
  },
  methods: {
    async getWorks() {
      const workData = await this.dbWork.getDocsData();

      for await (const work of workData) {
        const techImages = [];
        work.title = stringToCapitilize(work.title);
        work.img = await this.storeWork.getImageFromStorage(work.img);

        const techPaths = await this.getPathTech(work.techList);
        techPaths.forEach(async (techPath) => {
          techImages.push(await this.storeWork.getImageFromStorage(techPath));
        });
        work.techList = techImages;
      }

      this.works = [...this.sortWorks(workData)];
      this.isLoad = false;
    },

    async getPathTech(techNames) {
      const techData = await this.dbTech.getDocData("techs");
      const techPaths = [];
      techNames.forEach((techName) => {
        techPaths.push(techData[techName]);
      });
      return techPaths;
    },

    sortWorks(works) {
      const sortedWorks = works.sort((prevWork, nextWork) => {
        return prevWork.sort - nextWork.sort;
      });
      return sortedWorks;
    },
  },
};
</script>
