<template>
  <transition name="light-speed" appear>
    <section class="bio">
      <div class="container">
        <div class="bio__wrapper">
          <div class="bio__head bio-head">
            <div class="bio-head__hero">
              <img
                src="@/assets/img/avatar.svg"
                alt="Avatar"
                class="bio-head__img"
              />
              <div class="bio-head__headers">
                <h2 class="bio-head__title title">Зонь Олег</h2>
                <h4 class="bio-head__subtitle subtitle">
                  Junior Front-end developer
                </h4>
              </div>
              <toggle-theme class="bio__toggle-theme" />
            </div>

            <div class="bio-head__contacts bio-contacts">
              <a
                v-for="contact in contactsItems"
                :key="contact.href"
                :href="contact.href"
                target="blank"
                class="bio-contacts__item"
                ><img
                  :src="contact.img"
                  alt="Icon"
                  class="bio-contacts__item-img"
              /></a>
            </div>
          </div>

          <div class="bio__content bio-content">
            <ul class="skills-list">
              <li
                class="skills-list__item"
                v-for="skill in skillsItems"
                :key="skill.name"
                :style="{ '--pulsar-color': skill.color }"
              >
                <img
                  :src="skill.image"
                  alt="Icon"
                  class="skills-list__item-img"
                />
                <span class="skills-list__item-text text">{{
                  skill.name
                }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </section>
  </transition>

  <section class="portfolio">
    <div class="container">
      <div class="portfolio__wrapper">
        <transition name="fade" appear>
          <h2 class="portfolio__title title">Портфоліо</h2>
        </transition>
        <div class="portfolio__work-items">
          <router-view></router-view>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import WorkPreview from "@/components/WorkPreview.vue";
import WorkInfo from "@/components/WorkInfo.vue";
import ToggleTheme from "@/components/ToggleTheme.vue";
import { FirebaseAPI } from "@/firebaseAPI/firebaseAPI";
import { FirebaseStorage } from "./firebaseAPI/firebaseStorage";

export default {
  mounted() {
    this.getSkillData();
    this.getContactsData();
    document.title = "Portfolio - Junior Front-end Developer";
  },
  data() {
    return {
      dbSkill: new FirebaseAPI("skills"),
      dbContacts: new FirebaseAPI("contacts"),
      storageFb: new FirebaseStorage("skills"),
      skillsItems: [],
      contactsItems: [],
    };
  },
  components: {
    ToggleTheme,
    WorkInfo,
    WorkPreview,
  },
  methods: {
    async getSkillData() {
      const skillData = await this.dbSkill.getDocData("skillDoc");
      for (const skill in skillData) {
        skillData[skill].image = await this.storageFb.getImageFromStorage(
          skillData[skill].image
        );
        skillData[skill].name = skill;
      }
      this.skillsItems = skillData;
    },
    async getContactsData() {
      const contactData = await this.dbContacts.getDocData("contactDoc");
      this.contactsItems = contactData.contactList;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/style";

.title {
  font-weight: 700;
  font-size: 24px;
  line-height: 1em;
  transition: color $themeTime ease-in;
}

.subtitle {
  font-weight: 600;
  font-size: 18px;
  line-height: 1em;
  transition: color $themeTime ease-in;
}

.bio {
  width: 100%;
  transition: background-color $themeTime ease-in;

  &__wrapper {
    padding: 20px 0;
  }

  &__content {
    padding: 15px 0 10px;
  }

  &__toggle-theme {
    position: absolute;
    top: 0;
    right: 0;

    @media (max-width: (map.get($breakpoints, "md") - 0.02)) {
      top: auto;
    }
  }

  &__skills {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 0;
  }
}

.bio-contacts {
  display: flex;
  align-items: center;

  @media (max-width: 767.98px) {
    align-self: flex-end;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 50px;
    box-shadow: $shadow;
    border: none;
    border-radius: 50%;
    margin: 0 5px;
    transition: box-shadow 0.3s ease-out, background-color $themeTime ease-in;

    @media (any-hover: hover) {
      &:hover {
        box-shadow: 0 0 15px rgba(#000, 0.2);
      }
    }
  }

  &__item-img {
    height: 30px;
    width: 30px;
  }
}

.bio-head {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-right: 35px;

  @media (max-width: 767.98px) {
    flex-direction: column;
    padding-right: 0;
  }

  &__hero {
    display: flex;
    align-items: center;

    @media (max-width: 767.98px) {
      margin: 0 0 10px;
    }
  }

  &__img {
    height: 80px;
    width: 80px;
    padding: 5px;
    object-fit: cover;
    border: 2px solid #0500ff;
    border-radius: 50%;
    background-color: #fff;
  }

  &__headers {
    margin-left: 10px;
  }
}

.skills-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__item {
    width: 80px;
    height: 80px;
    border-radius: 15px;
    padding: 10px;
    text-align: center;
    margin: 5px;
    animation: pulsar 2s forwards infinite;
    transition: box-shadow 0.3s ease-out, background-color $themeTime ease-in;
  }

  &__item-img {
    width: 35px;
    height: 35px;
    margin: 0 auto 10px;
    object-fit: contain;
  }

  &__item-text {
    font-size: 12px;
    color: var(--pulsar-color);
  }
}

.portfolio {
  width: 100%;
  padding: 20px 0 0;

  &__title {
    text-align: center;
  }

  &__work {
    margin: 20px 0;
  }

  &__work-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
    margin: 20px 0;
    position: relative;

    @media (max-width: (map.get($breakpoints, "lg") - 0.02)) {
      justify-content: center;
    }
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 1.2s ease-out;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
