import config from "./firebaseConfig";

import { initializeApp } from "firebase/app";
import {
  getFirestore,
  collection,
  getDocs,
  doc,
  getDoc,
  setDoc,
  updateDoc,
  deleteDoc,
  deleteField,
} from "firebase/firestore";

export class FirebaseAPI {
  constructor(dbName) {
    this._firebase = initializeApp(config);
    this._db = getFirestore(this._firebase);
    this.dbName = dbName;
    this._docs = [];
  }

  getFirebaseInit() {
    return this._firebase;
  }

  async getFirestoreDocs() {
    const querySnapshot = await getDocs(collection(this._db, this.dbName));
    const docs = [];

    querySnapshot.forEach((doc) => {
      docs.push(doc);
    });

    return docs;
  }

  async addDocToDb(workData, docDbName = "") {
    await setDoc(doc(this._db, this.dbName, docDbName), workData);
  }

  async updateDocData(docData, docName) {
    if (!docName) {
      return;
    }
    const docRef = doc(this._db, this.dbName, docName);
    await updateDoc(docRef, docData);
  }

  async getDocsMeta() {
    return this.getFirestoreDocs().then((docs) => {
      return docs.map((doc) => {
        return {
          text: doc.data().title,
          value: doc.id,
        };
      });
    });
  }

  async getDocsData() {
    return this.getFirestoreDocs().then((docs) => {
      return docs.map((doc) => {
        return doc.data();
      });
    });
  }

  async getDocData(docName) {
    if (!docName) {
      return;
    }

    const docRef = doc(this._db, this.dbName, docName);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      return docSnap.data();
    } else {
      console.log("No such document!");
    }
  }

  async deleteDoc(docName) {
    const docRef = doc(this._db, this.dbName, docName);
    await deleteDoc(docRef);
  }

  async deleteDocField(docName, field) {
    const docRef = doc(this._db, this.dbName, docName);
    await updateDoc(docRef, {
      [field]: deleteField(),
    });
  }
}
