<template>
  <label class="toggle-theme">
    <input
      class="toggle-theme__checkbox _visually-hidden"
      type="checkbox"
      v-model="isChecked"
    />
    <span class="toggle-theme__icon">
      <transition mode="out-in" name="bounce">
        <font-awesome-icon icon="fa-solid fa-sun" v-if="!isChecked" />
        <font-awesome-icon icon="fa-solid fa-moon" v-else />
      </transition>
    </span>
  </label>
</template>
<script>
export default {
  data() {
    return {
      isChecked: false,
    };
  },
  watch: {
    isChecked() {
      const body = document.body;
      body.classList.toggle("dark-theme");
      body.classList.toggle("light-theme");
    },
  },
};
</script>
<style lang="scss">
.toggle-theme {
  cursor: pointer;
  transition: transform 0.3s ease-in;

  @media (any-hover: hover) {
    &:hover {
      transform: rotate(180deg);
    }
  }

  &__icon {
    font-size: rem(20);
    color: $backBlue;
  }
}

.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1);
  }
}
</style>