<template >
  <div class="work-preview">
    <div class="work-preview__front preview-front">
      <div
        class="preview-front__content"
        @mouseenter="isHover = true"
        @mouseleave="isHover = false"
      >
        <img :src="workParams.img" alt="" class="preview-front__img" />
        <transition name="fade">
          <div class="work-preview__back preview-back" v-show="isHover">
            <a :href="workParams.links.code" class="work-link" target="_blank"
              ><font-awesome-icon
                class="work-link__icon"
                icon="fa-solid fa-code"
            /></a>
            <a :href="workParams.links.live" class="work-link" target="_blank"
              ><font-awesome-icon
                class="work-link__icon"
                icon="fa-solid fa-display"
            /></a>
            <router-link class="work-link" :to="`/work/${workParams.slug}`"
              ><font-awesome-icon
                class="work-link__icon"
                icon="fa-solid fa-info"
            /></router-link>
          </div>
        </transition>
      </div>
      <div class="preview-front__head">
        <h4 class="preview-front__title">{{ workParams.title }}</h4>
        <ul class="preview-front__tech-list tech-list">
          <li
            class="tech-list__item"
            v-for="techPath in workParams.techList"
            :key="techPath"
          >
            <img :src="techPath" alt="tech" class="tech-list__item-img" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["workParams"],

  data() {
    return {
      isHover: false,
    };
  },
  methods: {
    previewMouseEnterHandle() {
      this.isHover = true;
    },
  },
};
</script>
<style lang="scss">
.work-preview {
  max-width: 360px;
}

.preview-front {
  width: 100%;
  height: 100%;
  border-radius: $workRadiusBorder;

  &__content {
    position: relative;
    border-radius: $workRadiusBorder;
  }

  &__img {
    width: 360px;
    height: 270px;
    border-radius: $workRadiusBorder;
    box-shadow: 0px 25px 20px -21px rgba(0, 0, 0, 0.57);

    @media (max-width: (map.get($breakpoints, "md") - 0.02)) {
      width: 100%;
    }
  }

  &__head {
    display: flex;
    flex-direction: column;
    margin-top: 15px;
    padding: 0 15px;
  }

  &__title {
    font-weight: 500;
    font-size: rem(24);
    line-height: 1em;
    text-align: center;
    transition: color $themeTime ease-in;
  }

  &__tech-list {
    margin-top: 10px;
  }
}

.tech-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  &__item {
    &:not(&:last-child) {
      margin-right: 10px;
    }
  }

  &__item-img {
    height: 24px;
  }
}

.preview-back {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(#000, 0.7);
  border-radius: $workRadiusBorder;
  z-index: 100;
  cursor: pointer;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>